.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
}
.header .header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .header__body {
  display: flex;
  justify-content: end;
  align-items: start;
  margin-left: 89px;
  position: relative;
  top: 15px;
}
.header .header__text {
  color: #FFF;
  font-size: 16px;
  font-weight: 700;
  line-height: 120%;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 5px;
  display: block;
}
.header .header__text.--big {
  font-size: 18px;
}
.header .header__text-light {
  color: #FFF;
  font-size: 13.097px;
  font-weight: 400;
  line-height: 120%; /* 15.716px */
  text-transform: uppercase;
  text-align: center;
  display: block;
}
.header .header__burger {
  display: none;
  position: absolute;
  top: 42px;
  right: 0;
  justify-content: end;
  align-items: center;
  width: 28px;
  height: 20px;
  box-sizing: border-box;
  cursor: pointer;
}
.header .header__burger::before, .header .header__burger::after {
  content: "";
  width: 100%;
  height: 2px;
  background: #fff;
  display: block;
  position: absolute;
  top: 0;
  right: 0;
}
.header .header__burger::after {
  top: unset;
  bottom: 0;
}
.header .header__burger:hover .header__burger-item {
  max-width: 100%;
}
.header .header__burger._active {
  z-index: 1001;
  position: fixed;
  top: 42px;
  right: 20px;
}
.header .header__burger._active .header__burger-item {
  opacity: 0;
}
.header .header__burger._active::before, .header .header__burger._active::after {
  bottom: unset;
  right: unset;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  background: #e7e7e7;
}
.header .header__burger._active::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.header .header__burger-item {
  height: 2px;
  max-width: 80%;
  width: 100%;
  background: #f7a81e;
  transition: 0.3s;
}
.header .header__nav {
  margin: 0 10vw 0 9vw;
  flex-shrink: 0;
}
@media screen and (min-width: 1595px) {
  .header .header__nav {
    margin: 0 8vw 0 6vw;
  }
}
.header .header__nav-list {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 0;
  list-style-type: none;
  margin: 0;
}
.header .header__nav-item {
  margin-top: 0;
}
.header .header__nav-item:not(:last-child) {
  margin-right: 4.58vw;
}
.header .header__nav-item a {
  color: #FFF;
  font-size: 16px;
  font-weight: 600;
  line-height: 120%; /* 19.2px */
  text-transform: uppercase;
  position: relative;
}
.header .header__nav-item a::after {
  content: "";
  height: 4px;
  max-width: 0%;
  width: 100%;
  background: #F7A81E;
  display: block;
  position: absolute;
  top: 100%;
  left: 0;
  transition: 0.2s ease-in;
}
.header .header__nav-item a:hover::after {
  max-width: 100%;
}
.header .header__mobile-menu {
  display: flex;
