  align-items: center;
  color: #FFF;
  font-size: 12px;
  font-weight: 400;
  line-height: 130%; /* 15.6px */
}
.header .header__info-text:not(:last-child) {
  margin-right: 6px;
}
.header .header__info-text:not(:last-child)::after {
  content: "";
  height: 8px;
  width: 1px;
  background: #fff;
  display: block;
  margin-left: 6px;
}
.header .header__info-text img {
  margin-right: 3px;
