@mixin page-h1 {
  font-size: 60px;
  font-weight: 700;
  line-height: 120%;
  text-transform: uppercase;
  @media screen and (max-width: 1130px) {
    font-size: 40px;
  }
  @media screen and (max-width: 780px) {
    font-size: 36px; 
    line-height: 130%;
  }
}
@mixin page-h2 {
  font-size: 60px;
  font-weight: 700;
  line-height: 120%;
  text-transform: uppercase;
  @media screen and (max-width: 1130px) {
     font-size: 40px; 
  }
  @media screen and (max-width: 780px) {
    font-size: 28px; 
  }
}
@mixin page-h3 {
  font-size: 40px;
  font-weight: 700;
  line-height: 120%; /* 48px */
  text-transform: uppercase;
  @media screen and (max-width: 1130px) {
    font-size: 35px;
  }
  @media screen and (max-width: 780px) {
    font-size: 20px; 
  }
}

@mixin page-text-20 {
  font-size: 20px;
  font-weight: 400;
  line-height: 120%;
  @media screen and (max-width: 780px) {
    font-size: 16px;
  }
}
@mixin page-text-16 {
  font-size: 16px;
  font-weight: 400;
  line-height: normal;
}

@mixin page-btn-transparent {
  color: #133859;
  font-size: 20px;
  font-weight: 700;
  line-height: normal;
  text-transform: uppercase;
  padding: 18px 56px;
  box-sizing: border-box;
  border: 1px solid #133859;
  transition: .3s;
  text-decoration: none;
  outline: none;
  display: inline-block;

  &:hover {
    background: #133859;
    color: #fff;
  }
  @media screen and (max-width: 780px) {
    padding: 18px 32px;
    font-size: 16px;
  }
}

@mixin page-btn {
  color: #fff;
  font-size: 20px;
  font-weight: 700;
  line-height: normal;
  text-transform: uppercase;
  padding: 18px 56px;
  box-sizing: border-box;
  border: 1px solid #F7A81E;
  background: #f7a81e;
  transition: .3s;
  text-decoration: none;
  outline: none;
  display: inline-block;

  &:hover {
    background: transparent;
    color: #f7a81e;
  }
  @media screen and (max-width: 780px) {
    padding: 18px 32px;
    font-size: 16px;
  }
}