.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  .header__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .header__logo {

  }
  .header__body {
    display: flex;
    justify-content: end;
    align-items: start;
    margin-left: 89px;
    position: relative;
    top: 15px;
  }
  .header__wrap {

  }
  .header__text {
    color: #FFF;
    font-size: 16px;
    font-weight: 700;
    line-height: 120%;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 5px;
    display: block;

    &.--big {
      font-size: 18px;
    }
  }
  .header__text-light {
    color: #FFF;
    font-size: 13.097px;
    font-weight: 400;
    line-height: 120%; /* 15.716px */
    text-transform: uppercase;
    text-align: center;
    display: block;
  }
  .header__burger {
    display: none;
    position: absolute;
    top: 42px;
    right: 0;
    justify-content: end;
    align-items: center;
    width: 28px;
    height: 20px;
    box-sizing: border-box;
    cursor: pointer;

    &::before,
    &::after {
      content: '';
      width: 100%;
      height: 2px;
      background: #fff;
      display: block;
      position: absolute;
      top: 0;
      right: 0;
    }
    &::after {
      top: unset;
      bottom: 0;
    }

    &:hover .header__burger-item {
      max-width: 100%;
    }

    &._active {
      z-index: 1001;
      position: fixed;
      top: 42px;
      right: 20px;
      .header__burger-item {
        opacity: 0;
      }
      &::before,
      &::after {
        bottom: unset;
        right: unset;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(45deg);
        background: #e7e7e7;
      }
      &::after {
        transform: translate(-50%, -50%) rotate(-45deg);
      }
    }
  }
  .header__burger-item {
    height: 2px;
    max-width: 80%;
    width: 100%;
    background: #f7a81e;
    transition: .3s;
  }
  .header__nav {
    margin: 0 10vw 0 9vw;
    flex-shrink: 0;

    @media screen and (min-width: 1595px) {
      margin: 0 8vw 0 6vw;
    }
  }
  .header__nav-list {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 0;
    list-style-type: none;
    margin: 0;
  }
  .header__nav-item {
    margin-top: 0;
    &:not(:last-child) {
      // margin-right: 66px;
      margin-right: 4.58vw;
    }
    a {
      color: #FFF;
      font-size: 16px;
      font-weight: 600;
      line-height: 120%; /* 19.2px */
      text-transform: uppercase;
      position: relative;

      &::after {
        content: '';
        height: 4px;
        max-width: 0%;
        width: 100%;
        background: #F7A81E;
        display: block;
        position: absolute;
        top: 100%;
        left: 0;
        transition: .2s ease-in;
      }
      &:hover::after {
        max-width: 100%;
      }
    }
  }
  .header__mobile-menu {
    display: flex;
    justify-content: end;
    align-items: star;
  }
  .header__socials {

  }
  .header__info {
    display: flex;
    justify-content: start;
    align-items: center;
  }
  .header__info-text {
    display: flex;
    justify-content: start;
    align-items: center;
    color: #FFF;
    font-size: 12px;
    font-weight: 400;
    line-height: 130%; /* 15.6px */

    &:not(:last-child) {
      margin-right: 6px;
    }
    &:not(:last-child)::after {
      content: '';
      height: 8px;
      width: 1px;
      background: #fff;
      display: block;
      margin-left: 6px;
    }
    img {
      margin-right: 3px;
      flex-shrink: 0;
    }
  }
}
@media screen and (max-width: 1439px) {
  .header .header__nav {
    margin: 0 6vw 0 6vw;
  }
}
@media screen and (max-width: 1180px) {
  .directions .directions__item-count {
    font-size: 90px;
  }
  .header .header__body {
    position: static;
    top: 0;
  }
  .header .header__wrapper {
    justify-content: start;
    flex-direction: column;
    justify-content: start;
    align-items: start;
  }
  .header .header__body {
    margin-left: 0;
  }
  .header__logo {
    margin-bottom: 20px;
  }
  .header .header__mobile-menu {
    // display: none;
    transform: translateX(100%);
    opacity: 0;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 1000;
    height: 100vh;
    padding: 126px 57px 10px 30px;
    box-sizing: border-box;
    background: #fff;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    transition: .3s ease-in;

    &._active {
      transform: translateX(0);
      opacity: 1;
    }
  }
  .header .header__nav-item a,
  .header .header__info-text,
  .header .header__text.--big {
    color: #133859; 
  }
  .header .header__burger {
    display: flex;
  }
  .header .header__wrapper {
    position: relative;
  }
  .header .header__nav-list {
    flex-direction: column;
    align-items: start;
  }
  .header .header__nav-item:not(:last-child) {
    margin-right: 0;
    margin-bottom: 28px;
  }
  .header .header__nav {
    margin: 0 0 44px;
  }
  .header .header__info-text:not(:last-child)::after {
    background: #133859;
  }
}