.footer {
    background: #004F7C;
    padding: 33px 0 60px;
    color: #fff;
    .footer__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .footer__logo {
        img {

        }
    }
    .footer__body {
        display: flex;
        justify-content: space-between;
        align-items: start;
    }
    .footer__nav {
        margin-right: 21.46vw;
    }
    .footer__list {
        list-style-type: none;
        padding: 0;
        margin: 0;
        display: flex;
        justify-content: start;
        align-items: center;
    }
    .footer__item {
        &:not(:last-child) {
            margin-right: 4.58vw;
        }
        a {
            color: #FFF;
            font-size: 16px;
            font-weight: 600;
            line-height: 120%; /* 19.2px */
            text-transform: uppercase;
            position: relative;

            &:hover::after {
                max-width: 100%;
            }

            &::after {
                content: '';
                max-width: 0%;
                width: 100%;
                height: 4px;
                background: #F7A81E;  
                position: absolute;
                top: 100%;
                left: 0;    
                transition: .2s;          
            }
        }
    }
    .footer__socials {

    }
    .footer__phone {
        display: block;
        font-size: 18px;
        font-weight: 700;
        line-height: 120%; /* 21.6px */
        text-transform: uppercase;     
        color: #fff;   
    }
    .footer__info {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .footer__mini {
        display: flex;
        justify-content: start;
        align-items: center;
        color: #FFF;
        font-size: 12px;
        font-style: normal;
        font-weight: 400;
        line-height: 130%; /* 15.6px */
        img {
            margin-right: 2px;
        }
        &:not(:last-child) {
            margin-right: 5px;

            &::after {
                content: '';
                height: 8px;
                width: 1px;
                background: #fff;
                display: block;
                margin-left: 5px;
            }
        }
    }
}

@media screen and (max-width: 1130px) {
    .footer .footer__wrapper {
        flex-direction: column;
        justify-content: start;
        align-items: center;
    }
    .footer .footer__body {
        flex-direction: column;
        align-items: center;
        justify-content: start;
    }
    .footer .footer__nav {
        margin-right: 0;
        margin-bottom: 36px;
        margin-top: 36px;
    }
    .footer .footer__list {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: start;
    }
    .footer .footer__item:not(:last-child) {
        margin-right: 0;
        margin-bottom: 28px;
    }
    .footer__logo {
        max-width: 118px;
    }
    .footer {
        padding: 40px 0 67px;
    }
}